import {NgModule} from "@angular/core";
import {RestDatasource} from "./rest-datasource.service";
import {SectionRepository} from "./section-repository.service";
import {HttpModule} from "@angular/http";
import {TopicRepository} from "./topic-repository.service";


@NgModule({
  imports : [
    HttpModule
  ],
  providers: [
    RestDatasource,
    SectionRepository,
    TopicRepository,
  ]
})
export class ServiceModule{}



