import {MaterialModule} from "./material.module";
import {RouterModule} from "@angular/router";
import {CommonModule} from "@angular/common";
import {NgModule} from "@angular/core";
import { MainBootstrapComponent } from './main-bootstrap/main-bootstrap.component';
import { HeaderComponent } from './main-bootstrap/header/header.component';
import { LocatorComponent } from './main-bootstrap/locator/locator.component';

@NgModule({
  imports: [
    CommonModule,
    MaterialModule,
    RouterModule,
  ],
  declarations: [
    MainBootstrapComponent,
    HeaderComponent,
    LocatorComponent,
  ],
  exports: [
    MaterialModule,
    HeaderComponent,
    LocatorComponent,
  ]
})
export class SharedModule { }



