import {Injectable} from "@angular/core";
import {QUERY_BY_ID, QUERY_BY_NAME, QUERY_REQUEST_NAME, RestDatasource} from "./rest-datasource.service";
import {Observable} from "rxjs/Observable";
import {Topic} from "../model/topic.model";
import {Paragraph} from "../model/paragraph.model";

@Injectable()
export class TopicRepository {
  readonly topicUrl: string = 'topic';
  readonly paragraphUrl: string = 'paragraph';

  constructor(private dataSource: RestDatasource) {}

  getNamesBySectionName(sectionName: string): Observable<string[]> {
    return this.dataSource.get(`${this.topicUrl}/category/${sectionName}${QUERY_REQUEST_NAME}`);
  }

  byName(topicName: string): Observable<Topic> {
    return this.dataSource.get(this.topicUrl +"/" + topicName + QUERY_BY_NAME);
  }

  getParagraphsById(topicId: number): Observable<Paragraph[]> {
    return this.dataSource.get(this.paragraphUrl+"/" + topicId + QUERY_BY_ID);
  }
}



