import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {SharedModule} from "../shared/shared.module";
import {ServiceModule} from "../service/service.module";
import { SectionBootstrapComponent } from './section-bootstrap.component';
import {RouterModule} from "@angular/router";
import { SectionSidenavComponent } from './section-sidenav/section-sidenav.component';
import { SectionTopiclistComponent } from './section-topiclist/section-topiclist.component';
import { SectionMainComponent } from './section-main/section-main.component';
import { TopicComponent } from './topic/topic.component';
import {SectionRoutingModule} from "./section-routing.module";

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule,
    ServiceModule,
    SectionRoutingModule,
  ],
  declarations: [
    SectionBootstrapComponent,
    SectionSidenavComponent,
    SectionTopiclistComponent,
    SectionMainComponent,
    TopicComponent
  ]
})
export class SectionModule { }



