import { Component, OnInit } from '@angular/core';
import {TopicRepository} from "../../service/topic-repository.service";
import {ActivatedRoute, Params} from "@angular/router";
import {Topic} from "../../model/topic.model";
import {Paragraph} from "../../model/paragraph.model";

@Component({
  selector: 'app-topic',
  templateUrl: './topic.component.html',
  styleUrls: ['./topic.component.css']
})
export class TopicComponent implements OnInit {
  topicName: string;
  topic: Topic;

  constructor(private topicRepository: TopicRepository,
              private route: ActivatedRoute) {}

  ngOnInit() {
    this.route.params.subscribe((params: Params) => {
      this.topicName = params['topicName'];
      this.topicRepository.byName(this.topicName)
        .subscribe((_topic: Topic) => {
          this.topic = _topic;
          this.topicRepository
            .getParagraphsById(this.topic.id)
            .subscribe((_paragraphs: Paragraph[]) => {this.topic.paragraphs = _paragraphs; console.log(this.topic.paragraphs)});
        })
    });
  }

}



