import {Inject, Injectable} from "@angular/core";
import {Http, Request, RequestMethod, Response} from "@angular/http";
import {BACKEND_URL_TOKEN} from "../app.tokens";
import {Observable} from "rxjs/Observable";
import 'rxjs/Rx';
export const QUERY_REQUEST_NAME = "?request=name";
export const QUERY_BY_NAME = "?by=name";
export const QUERY_BY_ID = "?by=id";

@Injectable()
export class RestDatasource {
  constructor(private http: Http,
              @Inject(BACKEND_URL_TOKEN) private backendUrl: string ) {
    console.log(this.backendUrl);
  }

  get(url: string, params?: any): Observable<any> {
    return this.request(RequestMethod.Get, url);
  }

  post(url: string, body?: any): Observable<any> {
    return this.request(RequestMethod.Post, url, body);
  }

  request(method: RequestMethod, url?: string, body?: any): Observable<any>{
    let request = new Request({
      method: method,
      url: this.backendUrl + url,
      body: body,
    });
    return this.http.request(request)
      .map((response:Response) => response.json());
  }


}



