import { Component, OnInit } from '@angular/core';
import {SectionRepository} from "../../service/section-repository.service";
import {ActivatedRoute, Params} from "@angular/router";
import {TopicRepository} from "../../service/topic-repository.service";
import {Section} from "../../model/section.model";

@Component({
  selector: 'app-section-main',
  templateUrl: './section-main.component.html',
  styleUrls: ['./section-main.component.css']
})
export class SectionMainComponent implements OnInit {
  section: Section;
  topicNames: string[];
  constructor(private route: ActivatedRoute,
              private sectionRepository: SectionRepository,
              private topicRepository: TopicRepository) {}

  ngOnInit() {
    this.route.params.subscribe((params: Params) => {
      let sectionName = params['sectionName'];
      this.sectionRepository
        .byName(sectionName)
        .subscribe((_section: Section) => {
          this.section = _section;
          this.topicRepository
            .getNamesBySectionName(this.section.name)
            .subscribe((_topicNames: string[]) => this.topicNames = _topicNames);
        });
    })
  }
}



