import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {SectionRepository} from "../../../service/section-repository.service";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  sectionNames: string[] ;
  @Output() onSidenavToggle:EventEmitter<void> = new EventEmitter<void>();
  constructor(private sectionRepository: SectionRepository){}

  ngOnInit() {
    this.sectionRepository
      .getNames()
      .subscribe((_sectionNames:string[]) => this.sectionNames = _sectionNames);
  }


}



