import {NgModule} from "@angular/core";
import {RouterModule, Routes} from "@angular/router";
import {SectionBootstrapComponent} from "./section-bootstrap.component";
import {SectionMainComponent} from "./section-main/section-main.component";
import {TopicComponent} from "./topic/topic.component";

const routes: Routes = [
  {path: 'section/:sectionName', component: SectionBootstrapComponent,
    children: [
      {path: '', component: SectionMainComponent, pathMatch: 'full'},
      {path: ':topicName', component: TopicComponent },
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SectionRoutingModule{}



