import {Injectable} from "@angular/core";
import {QUERY_BY_NAME, QUERY_REQUEST_NAME, RestDatasource} from "./rest-datasource.service";
import {Observable} from "rxjs/Observable";
import {Section} from "../model/section.model";

@Injectable()
export class SectionRepository {
  readonly sectionUrl: string = 'section';

  constructor(private dataSource: RestDatasource) {}

  getNames(): Observable<string[]> {
    return this.dataSource.get(this.sectionUrl + QUERY_REQUEST_NAME);
  }

  byName(name: string): Observable<Section> {
    return this.dataSource.get(`${this.sectionUrl}/${name}${QUERY_BY_NAME}`);
  }
}




